import Bridge from 'react-devtools-shared/src/bridge';
import Store from 'react-devtools-shared/src/devtools/store';
import {getSavedComponentFilters} from 'react-devtools-shared/src/utils';
import {registerDevToolsEventLogger} from 'react-devtools-shared/src/registerDevToolsEventLogger';
import {Server} from 'ws';
import {join} from 'path';
import {readFileSync} from 'fs';
import {__DEBUG__} from 'react-devtools-shared/src/constants';
import { parseSourceUrl } from './utils';
import type {
  StatusListener,
  StatusTypes,
  OnDisconnectedCallback,
  OnDataCallback,
  OnInspectionCallback,
  InspectedElementPayload,
} from './types';


import type {FrontendBridge} from 'react-devtools-shared/src/bridge';
import {symbolicateSourceWithCache} from 'react-devtools-shared/src/symbolicateSource';

let statusListener: StatusListener = (
  message: string,
  status?: StatusTypes,
) => {
  log('Status', message, status);
};

function setStatusListener(value: StatusListener): DevtoolsType {
  statusListener = value;
  return Devtools;
}

let disconnectedCallback: OnDisconnectedCallback = () => {};
function setDisconnectedCallback(value: OnDisconnectedCallback): DevtoolsType {
  disconnectedCallback = value;
  return Devtools;
}

let dataCallback: OnDataCallback = () => {};
function setDataCallback(value: OnDataCallback): DevtoolsType {
  dataCallback = value;
  return Devtools;
}

let inspectionCallback: OnInspectionCallback = () => {};
function setInspectionCallback(value: OnInspectionCallback): DevtoolsType {
  inspectionCallback = value;
  return Devtools;
}

function startInspectingHost() {
  if (!bridge) {
    console.log('no bridge');
    return Devtools;
  }
  bridge.send('startInspectingHost');
  return Devtools;
}

function stopInspectingHost() {
  if (!bridge) {
    console.log('no bridge');
    return Devtools;
  }
  bridge.send('stopInspectingHost', false);
  return Devtools;
}

let bridge: FrontendBridge | null = null;
let store: Store | null = null;

const log = (...args: Array<mixed>) => console.log('[React DevTools]', ...args);
log.warn = (...args: Array<mixed>) => console.warn('[React DevTools]', ...args);
log.error = (...args: Array<mixed>) =>
  console.error('[React DevTools]', ...args);

function debug(methodName: string, ...args: Array<mixed>) {
  if (__DEBUG__) {
    console.log(
      `%c[core/standalone] %c${methodName}`,
      'color: teal; font-weight: bold;',
      'font-weight: bold;',
      ...args,
    );
  }
}

let i = 0;

function reload() {
  if (!bridge || !store) {
    console.log('no bridge or store');
    return;
  }
  const localBridge = bridge;
  const localStore = store;

  localBridge.addListener('selectElement', elementId => {
    const rendererID = localStore.getRendererIDForElement(elementId);
    // const element = localStore.getElementByID(elementId);

    if (rendererID === null) {
      console.log('no renderer id');
      return;
    }

    const payload = {
      id: elementId,
      rendererID,
      requestID: i++,
      path: null,
      forceFullData: true,
    };

    localBridge.send('inspectElement', payload);
  });
  
  localBridge.addListener('startInspectingHost', () => {
    inspectionCallback(true);
  })

  localBridge.addListener('stopInspectingHost', () => {
    inspectionCallback(false);
  })

  localBridge.addListener('inspectedElement', async data => {
    if (data == null) return null;

    if (data.type === 'full-data') {

      if (data.value.source == null) {
        return Promise.resolve(null)
      };

      const {sourceURL, line, column} = data.value.source;
      
      const symbolicatedSource = await symbolicateSourceWithCache(
        fetchFileWithCaching,
        sourceURL,
        line,
        column,
      );
      
      if (symbolicatedSource) {
        const enrichedData: InspectedElementPayload = {
          ...data,
          value: {
            ...data.value,
            parsedSource: {
              symbolicated: true,
              source: parseSourceUrl(symbolicatedSource.sourceURL),
              line: symbolicatedSource.line,
              column: symbolicatedSource.column
            }
          }
        }
        dataCallback(enrichedData);
      } else {
        const enrichedData: InspectedElementPayload = {
          ...data,
          value: {
            ...data.value,
            parsedSource: {
              symbolicated: false,
              source: parseSourceUrl(sourceURL),
              line: line,
              column: column
            }
          }
        }
        dataCallback(enrichedData);
      }
      
    } else {
      dataCallback(data);
    }
  });
}

function onDisconnected() {
  disconnectedCallback();
}

function onError({code, message}: $FlowFixMe) {
  console.error(`Error: ${code} ${message}`);
}

function initialize(socket: WebSocket) {
  const listeners = [];
  socket.onmessage = event => {
    let data;
    try {
      if (typeof event.data === 'string') {
        data = JSON.parse(event.data);

        if (__DEBUG__) {
          debug('WebSocket.onmessage', data);
        }
      } else {
        throw Error();
      }
    } catch (e) {
      log.error('Failed to parse JSON', event.data);
      return;
    }
    listeners.forEach(fn => {
      try {
        fn(data);
      } catch (error) {
        log.error('Error calling listener', data);
        throw error;
      }
    });
  };

  bridge = new Bridge({
    listen(fn) {
      listeners.push(fn);
      return () => {
        const index = listeners.indexOf(fn);
        if (index >= 0) {
          listeners.splice(index, 1);
        }
      };
    },
    send(event: string, payload: any, transferable?: Array<any>) {
      if (socket.readyState === socket.OPEN) {
        socket.send(JSON.stringify({event, payload}));
      }
    },
  });

  ((bridge: any): FrontendBridge).addListener('shutdown', () => {
    socket.close();
  });

  // $FlowFixMe[incompatible-call] found when upgrading Flow
  store = new Store(bridge, {
    checkBridgeProtocolCompatibility: true,
    supportsTraceUpdates: true,
    supportsClickToInspect: true,
  });

  log('Connected');
  statusListener('DevTools initialized.', 'devtools-connected');
  reload();
}

function connectToSocket(socket: WebSocket): {close(): void} {
  socket.onerror = err => {
    onDisconnected();
    log.error('Error with websocket connection', err);
  };
  socket.onclose = () => {
    onDisconnected();
    log('Connection to RN closed');
  };
  initialize(socket);

  return {
    close: function () {
      onDisconnected();
    },
  };
}

type ServerOptions = {
  key?: string,
  cert?: string,
};

type LoggerOptions = {
  surface?: ?string,
};

function startServer(
  port: number = 8097,
  host: string = 'localhost',
  httpsOptions?: ServerOptions,
  loggerOptions?: LoggerOptions,
  attempts: number = 0,
): DevtoolsType {
  registerDevToolsEventLogger(loggerOptions?.surface ?? 'standalone');

  const useHttps = !!httpsOptions;
  const httpServer = useHttps
    ? require('https').createServer(httpsOptions)
    : require('http').createServer();
  const server = new Server({server: httpServer, maxPayload: 1e9});
  let connected: WebSocket | null = null;

  server.on('connection', (socket: WebSocket) => {
    if (connected !== null) {
      connected.close();
      log.warn(
        'Only one connection allowed at a time.',
        'Closing the previous connection',
      );
    }
    connected = socket;
    socket.onerror = error => {
      connected = null;
      onDisconnected();
      log.error('Error with websocket connection', error);
    };
    socket.onclose = () => {
      connected = null;
      onDisconnected();
      log('Connection to RN closed');
    };
    initialize(socket);
  });

  function handleWSError(event: $FlowFixMe) {
    if (event.code === 'EADDRINUSE') {
      if (attempts < 10) {
        log.warn(`Port ${port} in use, trying port ${port + 1}...`);
        // Try next port
        return startServer(
          port + 1,
          host,
          httpsOptions,
          loggerOptions,
          attempts + 1,
        );
      } else {
        onError(event);
        statusListener(
          'Failed to start the server after 10 attempts.',
          'error',
        );
      }
    } else {
      onError(event);
      statusListener('Failed to start the server.', 'error');
    }
    return Devtools;
  }

  server.on('error', handleWSError);

  function handleError(event: $FlowFixMe) {
    onError(event);
    statusListener('Failed to start the server.', 'error');
    return Devtools;
  }

  httpServer.on('error', handleError);

  httpServer.on('request', (request: $FlowFixMe, response: $FlowFixMe) => {
    // Serve a file that immediately sets up the connection.
    const backendFile = readFileSync(join(__dirname, 'backend.js'));

    const savedPreferencesString = `
      window.__REACT_DEVTOOLS_COMPONENT_FILTERS__ = ${JSON.stringify(
        getSavedComponentFilters(),
      )};`;

    response.end(
      savedPreferencesString +
        '\n;' +
        backendFile.toString() +
        '\n;' +
        'ReactDevToolsBackend.initialize();' +
        '\n' +
        `ReactDevToolsBackend.connectToDevTools({port: ${port}, host: '${host}', useHttps: ${
          useHttps ? 'true' : 'false'
        }});
        `,
    );
  });

  httpServer.listen(port, () => {
    Devtools.currentPort = port;
    statusListener(
      'The server is listening on the port ' + port + '.',
      'server-connected',
    );
  });

  return Devtools;
}

const resourceCache: Map<string, string> = new Map();

// As a potential improvement, this should be done from the backend of RDT.
// Browser extension is doing this via exchanging messages
// between devtools_page and dedicated content script for it, see `fetchFileWithCaching.js`.
async function fetchFileWithCaching(url: string): Promise<string> {
  if (resourceCache.has(url)) {
    const content = resourceCache.get(url);
    if (content != null) {
      return Promise.resolve(content);
    }
  }

  return fetch(url)
    .then(data => data.text())
    .then(content => {
      resourceCache.set(url, content);
      return content;
    });
}

const Devtools: DevtoolsType = {
  currentPort: null,
  connectToSocket,
  setStatusListener,
  setDisconnectedCallback,
  startServer,
  setDataCallback,
  setInspectionCallback,
  startInspectingHost,
  stopInspectingHost,
};

type DevtoolsType = {
  currentPort: ?number,
  connectToSocket: (socket: WebSocket) => {close(): void},
  setStatusListener: (value: StatusListener) => DevtoolsType,
  setDisconnectedCallback: (value: OnDisconnectedCallback) => DevtoolsType,
  setDataCallback: (value: OnDataCallback) => DevtoolsType,
  setInspectionCallback: (value: OnInspectionCallback) => DevtoolsType,
  startInspectingHost: () => DevtoolsType,
  stopInspectingHost: () => DevtoolsType,
  startServer: (
    port: number,
    host: string,
    httpsOptions?: ServerOptions,
    loggerOptions?: LoggerOptions,
  ) => DevtoolsType,
};

export default Devtools;
